.burger-button {
  position: absolute;
  content: "";
  z-index: 3;
  display: block;
  width: 24px;
  height: 24px;
  right: 30px;
  bottom: 20px;
  background-color: transparent;
  border: 0;
  cursor: pointer;
}
.burger-button::before {
  position: absolute;
  content: "";
  z-index: 3;
  display: block;
  width: 24px;
  height: 4px;
  top: 0;
  left: 0;
  box-shadow: 0 4px 0 0 #81B3D2, 0 12px 0 0 #81B3D2, 0 20px 0 0 #81B3D2;
