.burger-button:hover::before {
  box-shadow: 0 4px 0 0 rgba(129, 179, 210, 0.6), 0 12px 0 0 rgba(129, 179, 210, 0.6), 0 20px 0 0 rgba(129, 179, 210, 0.6);
