.map-api__content {
  height: 350px;
  margin-top: 40px;
  margin-bottom: 40px;
  background-color: #81B3D2;
}
.map-api__content--before {
  position: relative;
}
.map-api__content--before::before {
  position: absolute;
  z-index: 2;
  content: "";
  display: block;
  width: 27px;
  height: 27px;
  top: 50%;
  left: 50%;
  background-color: #81B3D2;
  border: 4px solid #ffffff;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}
.map-api__title--hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.map-api__img {
