.cancel-button {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 15px;
  right: 30px;
  background-color: transparent;
  border: none;
}
.cancel-button::before {
  position: absolute;
  z-index: 2;
  content: "";
  display: block;
  width: 30px;
  height: 4px;
  top: 11px;
  right: -2px;
  background-color: #ffffff;
  transform: rotate(-45deg);
}
.cancel-button::after {
  position: absolute;
  z-index: 5;
  content: "";
  display: block;
  width: 30px;
  height: 4px;
  top: 11px;
  right: -2px;
  background-color: #ffffff;
  transform: rotate(45deg);
}
.cancel-button:hover::before,
.cancel-button:hover::after {
  background-color: rgba(255, 255, 255, 0.6);
}
.cancel-button:active::before,
.cancel-button:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .cancel-button {
    display: none;
  }
}
